body{
  background-color:rgb(85, 209, 193);
}
nav{
  width: 100%;
  height: 7vh;
}
ul{
  background-color: rgb(74, 163, 163); 
  display: flex;
  margin: auto;
  padding: 1vh;
}
li{
  padding: 1vh;
  margin:0vh 0vh 0vh 27vh;
  list-style-type: none;
}
a{
  text-decoration: none;
  color: white;
  font-family: fantasy;
  font-size: 2.5vh;
  transition: 0.3s;

}
a:hover{
  color: black;
  background-color: white;
  padding: 1.5vh;
  border-radius: 1.5vh;
  transition: 0.3s;
}
.h1{
  transition: 0.3s;
  font-size: 5vh;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  text-align: center;
  color:black;
  opacity: 0.6;
}
p{
  padding : 10px;
  margin : 20px;
  text-align : center;
  font-family: sans-serif;
}
@media only screen and (max-width: 1016px) {
  li {
    padding: 0vh;
    margin: 0vh 0vh 0vh 24vh;
    list-style-type: none;
}
  a{
    text-decoration: none;
    color: white;
    font-family: fantasy;
    font-size: 2.3vh;
    transition: 0.3s;
  
  }
  a:hover{
    color: black;
    background-color: white;
    padding: 0.5vh 2vh 0.5vh 2vh;
    border-radius: 1.5vh;
    transition: 0.3s;
  }
}
